import axios from 'axios';
import router from './router/index';
import { adminRefreshToken } from './api/admin';
import moment from 'moment';

// axios.defaults.baseURL = 'http://testingnbackend.somee.com/api/';

const SkipAuthHeader = ['auth/admin/login', 'auth/admin/refresh'];
const defaultOptions = {
  baseURL: 'http://testingnbackend.somee.com/api/',
  // headers: { 'Content-Type': 'application/json' },
};
const axiosInstance = axios.create(defaultOptions);

axiosInstance.interceptors.request.use((config) => {
  const savedAdminInformation = JSON.parse(localStorage.getItem('admin'));
  if (SkipAuthHeader.includes(config.url)) {
    return config;
  }
  let canDoRefreshToken =
    moment(savedAdminInformation.accessTokenExpiration).isAfter(moment.now()) &&
    moment(savedAdminInformation.refreshTokenExpiration).isBefore(moment.now());

  if (canDoRefreshToken) {
    adminRefreshToken()
      .then((tResp) => {
        localStorage.setItem('admin', JSON.stringify(tResp.data));
        return config;
      })
      .catch(() => {
        router.push({ name: 'Signin' });
        return config;
      });
  }
  config.headers.Authorization = savedAdminInformation?.accessToken
    ? `Bearer ${savedAdminInformation?.accessToken}`
    : '';
  return config;
});

axiosInstance.interceptors.response.use(
  (config) => config,
  (error) => {
    if (error.response.status === 401) {
      router.push({ name: 'Signin' });
    }
  }
);

export default axiosInstance;
