import router from '../../router/index';
import { adminLogin, getCurrentAdmin } from '../../api/admin';

export default {
  namespaced: true,
  state: {
    admin: {},
    loading: false,
    loginFailed: false,
  },

  actions: {
    login({ commit, state }, payload) {
      if (state.loading) return;
      commit('SET_LOADING_INPROGRESS', true);
      commit('SET_LOADING_FAILED', false);
      return adminLogin(payload)
        .then((data) => commit('SET_ADMIN_AUTH_DATA', data.data))
        .catch(() => {
          commit('SET_LOADING_INPROGRESS', false);
          commit('SET_LOADING_FAILED', true);
        });
    },

    getCurrentAdmin() {
      return getCurrentAdmin()
        .then((data) => console.log(data))
        .catch((err) => {
          console.log(err);
        });
    },
  },

  mutations: {
    SET_LOADING_INPROGRESS: (state, value) => (state.loading = value),
    SET_LOADING_FAILED: (state, value) => (state.loginFailed = value),
    SET_ADMIN_AUTH_DATA: (state, payload) => {
      state.admin = payload;
      localStorage.setItem('admin', JSON.stringify(state.admin));
      state.loading = false;
      router.push({ name: 'Dashboard' });
    },
  },
};
