import axiosInstance from '../axios-instance';

export function getAdmins(paginationParams = { PageNumber: 1, PageSize: 10 }) {
  return axiosInstance.get('cms/admins', { params: { ...paginationParams } });
}

export function addAdmin(paylaod) {
  return axiosInstance.post('cms/admins', paylaod);
}

export function getAdmin(id) {
  return axiosInstance.get(`cms/admins/${id}`);
}

export function updateAdmin(paylaod) {
  return axiosInstance.put(`cms/admins/${paylaod.id}`, paylaod);
}

export function deleteAdmin(id) {
  return axiosInstance.delete(`cms/admins/${id}`);
}

export function resetPasswordForAdmin(paylaod) {
  return axiosInstance.put(`cms/admins/${paylaod.id}/reset-password`, paylaod);
}
